import { Component, OnInit } from '@angular/core';
import { ConfirmationService } from 'primeng/api';
import { MessageService } from 'primeng/api';
import { DialogService } from 'primeng/dynamicdialog';
import { forkJoin, Observable } from 'rxjs';
import { OrderRequestComponent } from '../../order-management/order-request/order-request.component';
import { RepairRequestResponse } from '../models/repair-request-response';
import { RepairRequestsService } from '../services/repair-requests.service';

@Component({
  selector: 'app-list-repair-requests',
  templateUrl: './list-repair-requests.component.html',
  styleUrls: [
    './list-repair-requests.component.css',
    '../../../demo/view/tabledemo.scss'
  ],
  styles: [`
        :host ::ng-deep .p-dialog .product-image {
            width: 150px;
            margin: 0 auto 2rem auto;
            display: block;
        }
    `],
  providers: [MessageService, ConfirmationService, DialogService]
})
export class ListRepairRequestsComponent implements OnInit {
  statuses = [
    { no: 1, name: 'Booked', icon: 'pi-calendar' },
    { no: 2, name: 'Repairing', icon: 'pi-cog' },
    { no: 3, name: 'Finshed', icon: 'pi-thumbs-up' },
    { no: 4, name: 'Collected', icon: 'pi-user' },
    { no: 5, name: 'Canceled', icon: 'pi-user' },
  ]
  productDialog: boolean;
  selecteditems: RepairRequestResponse[];

  submitted: boolean;
  pageNo: number = 1;
  ListRequestCount: number;
  repairRequestList: RepairRequestResponse[] = []

  constructor(
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    public dialogService: DialogService,
    private RepairRequestsService: RepairRequestsService
  ) { }

  ngOnInit() {
    this.getListRequest();

  }



  getListRequest() {
    this.RepairRequestsService.getlistRepairRequest({ pageNumber: this.pageNo }).subscribe(data => {
      this.repairRequestList = data.data;
      this.ListRequestCount = data.count;
    })
  }

  loadMoreListRequestData(event: any) {
    this.pageNo = event.page + 1;
    this.getListRequest();
  }

  deleteSelectedItems() {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete the selected Item?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        const productsDeletedIDs: number[] = []
        let deleteRequests: Observable<any>[] = []
        // this.repairRequestList = this.repairRequestList.filter(val => !this.selecteditems.includes(val));
        this.selecteditems.forEach(item => {
          deleteRequests.push(this.RepairRequestsService.deleteRepairRequest(item.id))
          productsDeletedIDs.push(item.id)
        })
        this.selecteditems = [];
        forkJoin(deleteRequests)
          .subscribe((res) => {
            this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Repair Request/s with Id/s ( ${productsDeletedIDs.toString()} ) Successfully Deleted`, life: 3000 });
          })
      }
    })
  }

  changeProductStatus(id: number, statusNo: number,) {
    this.RepairRequestsService.changeRepairRequestStatus(id, statusNo)
      .subscribe(
        (res) => {
          this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Request #${id} status successfully changed`, life: 3000 });
          this.getListRequest()
        }
      )
  }


  deleteItem(item: RepairRequestResponse) {
    this.confirmationService.confirm({
      message: 'Are you sure you want to delete request Number' + item.requestNumber + '?',
      header: 'Confirm',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.RepairRequestsService.deleteRepairRequest(item.id).subscribe(data => {
          if (data.succeeded) {
            this.getListRequest()
            this.removeItemFromSelectedItems(item.id)
            this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Repair request with Id #${item.id} Successfully Deleted`, life: 3000 });
          }
        })
      }
    });
  }

  // refresh the selected items if the deleted item is already selected
  removeItemFromSelectedItems(id: number) {
    const filteredItems = this.selecteditems.filter((_item: RepairRequestResponse) => {
      return _item.id != id
    })
    this.selecteditems = [...filteredItems]
  }




}
