import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { RepairRequestResponse } from '../models/repair-request-response';
import { UpdateRepairReqStatusRequest } from '../models/change-repair-req-status-reques.model';
import { ApiResponse } from '../../../models/api-response.model';

@Injectable({
  providedIn: 'root'
})
export class RepairRequestsService {
  prefix :string = "RepairRequest"
  get endPoint() : string { return `${environment.api}/${this.prefix}` }
  constructor(private http: HttpClient) { }


  getlistRepairRequest({ pageNumber = 1, pageSize = 10 }) {
    return this.http.get<ApiResponse<RepairRequestResponse[]>>(`${this.endPoint}/?PageNumber=${pageNumber}&PageSize=${pageSize}`)
  }

  changeRepairRequestStatus(repairRequestId:number,statusNo:number) {
    let reqBody = new UpdateRepairReqStatusRequest(repairRequestId,statusNo)
    return this.http.put<ApiResponse<number>>(`${this.endPoint}/updateStatus-${repairRequestId}`,reqBody)
  }

  deleteRepairRequest(id:number ) {
    return this.http.delete<ApiResponse<number>>(`${this.endPoint}/${id}`)
  }

}
